.aboutContainer {
    width: 230px;
    margin: 40px auto auto auto;
    left: 1029px; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    /* color: #222016;  */
}
 
.about {
    display: flex;
    flex-direction: column-reverse;
    margin: 0px auto 0px 20px;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

img {
    margin: 0px auto 20px auto;
    width: 230px;
    box-shadow: 10px 10px 60px 10px #B7AB9F; 
}

.aboutH1 {
    font-size: 28px;
    padding-bottom: 30px;
    text-align: center;

}

.aboutH2 {
    font-size: 23px;
    padding-bottom: 20px;
    text-align: center;
}

.aboutP {
    font-size: 17px;
    padding-bottom: 40px;
    width: 230px; 
    text-align: center;
}

button {
        display: flex;
        width: 200px;         
        border-radius: 6px;
        text-transform: uppercase;
        font-weight: bold;
        background-color:#28293E;
        border: 1px solid #28293E;
        line-height: 48px;
        margin: 7px auto 51px 15px;
        justify-content: center;
}
.aAbout {
    color: white;
    font-size: 14px;
}

@media (min-width: 768px){

    img {
    margin: 10px auto 40px auto;
    justify-items: center;
    width: 330px;
    }

    .aboutContainer {
        width: 600px;
        justify-content: center;
        align-items: center;
        margin: 30px auto 100px auto;
    }
    
    .aboutH1 {
        padding-bottom: 25px;
        font-size: 43px;
        margin-left: 0px;
    
    
    }
    
    .aboutH2 {
        padding-bottom: 20px;
        font-size: 37px;
        margin-left: 0px;
    }
    
    .aboutP {
        width: 520px;
        padding-bottom: 50px;
        font-size: 24px;
        margin-left: 0px;
    
    }
    
    button {
        margin: 0px auto 40px auto;
        justify-content: center;
        width: 250px;         
        border-radius: 6px;
        font-size: 20px;
        line-height: 50px;
    }
    .aAbout {
        font-size: 20px;
    }
} 

@media (min-width: 1366px){
    img {
        margin: 0px auto 230px auto;
        justify-items: flex-start;
        width: 450px;
}
    
          
